<template>
  <div class="aviator">
    <span>FUN MODE</span>
    <div id="aviator"></div>
    <LLoading1 :visible="loading"/>
  </div>
  <div class="aviatorBoard">
    <div v-for="(btt,index) in better" :key="index" :class="'aviatorBElt'+btt.state" class="aviatorBElt">
      <div class="aviatorBEltH"><div style="background-color:#2C2D30">Bet</div><div>Auto</div></div> 
      <div>
        <div class="aviatorBet">
          <div><input v-model="btt.bet"/>
            <img @click="btt.bet--" src="./img/minus.svg"/>
            <img @click="btt.bet++" src="./img/plus.svg"/>
          </div>
          <span><span @click="btt.bet=1">1 $</span><span @click="btt.bet=2">2 $</span></span>
          <span><span @click="btt.bet=5">5 $</span><span @click="btt.bet=10">10 $</span></span>
        </div>
        <span v-show="btt.state==0" class="aviatorBGreen" @click="btt.state=1">BET</span>
        <span v-show="btt.state==2" class="aviatorBOrange" @click="cashingOut(btt)">CASH OUT {{btt.cashOut}} $</span>
        <div style="padding:0px" v-show="btt.state==1" class="aviatorBRed">
          <span>Waiting for next round</span>
          <div @click="btt.state=0">CANCEL</div>
        </div>
      </div>
    </div>
  </div>
  <img style="display:none" ref="image" id="image" src="./img/aim.png"/>
 </template>
<script src="./aviator.js">
</script>
<style src="./aviator.css">
</style>
