import Konva from 'konva'
import LLoading1 from '@/components/lLoading1/LLoading1'

export default {
    name: 'aviator',
    emits:['cashing','maxAlt'],
    components:{
      LLoading1
    },
    data () {
        return {
          better: [{bet:0.10,state:0,cashOut:0.00},{bet:0.10,state:0,cashOut:0.00}],
          loading:false,
          //--- state:0 start, state:1 waiting,state:2 cashing
          //--- for the game
          anim:{},
          altitudeAnim:{},
          planeMove:{},
          backAnim:{},
          move:0,
          move1:0,
          step:1,
          counter:0,
          line:{},
          lineRegion:{},
          quad:{},
          altitude:1,
          maxAlt:1,
          range:[{min:1,max:2},{min:1,max:2},{min:1,max:2},{min:1,max:2},{min:1,max:2},
            {min:2,max:3},{min:2,max:3},
            {min:3,max:5},
            {min:5,max:30},{min:5,max:30}],
          altText:{},
          endText:{}
        }
    },
    methods: {
      cashingOut (better) {
        this.$emit('cashing',better.cashOut)
        better.state = 0
      },
      start () {
        this.quad.start.x(0)
        this.quad.start.y(374)
        this.quad.control.x(327)
        this.quad.control.y(295)
        this.quad.end.x(467)
        this.quad.end.y(115)
        //------
        this.better[0].cashOut = 0 
        this.better[1].cashOut = 0
        let range = this.range[Math.floor(Math.random()*9)]
        this.altitude = 1
        this.maxAlt = Math.random()*(range.max-range.min) + range.min
        //------
        this.move = 0
        this.move1 = 0
        this.step = 1
        this.counter = 0
        this.line.points([])
        this.lineRegion.points([])
        //--- other --------------
        this.altText.fill("white")
        this.endText.visible(false)
        this.line.visible(true)
        this.lineRegion.visible(true)
        //------ animation --------
        this.altitudeAnim.start()
        this.planeMove.start()
        this.backAnim.start()
      },
      launch: async (k)=>  {
        k.$emit('maxAlt',k.maxAlt)
        function waitDelay(t){
            return new Promise((resolve) => {
                setTimeout(resolve, t);
            })
        }
        if(k.better[0].state == 2){
          k.better[0].state = 0
          k.$emit('cashing',-k.better[0].bet)
        }
        if(k.better[1].state == 2){
          k.better[1].state = 0
          k.$emit('cashing',-k.better[1].bet)
        }
        await waitDelay(2000)
        k.backAnim.stop()
        k.altText.fill('transparent')
        k.endText.visible(false)
        k.loading = true
        await waitDelay(2000)
        k.better[0].state = k.better[0].state == 1  ? 2 : k.better[0].state
        k.better[1].state = k.better[1].state == 1  ? 2 : k.better[1].state
        k.loading = false
        k.start()
      },
    },
    mounted () {
        
      let width = 680;
      let height = 374;

      // function to build anchor point
      function buildAnchor(x, y) {
        var anchor = new Konva.Circle({
          x: x,
          y: y,
          radius: 20,
          stroke: '#666',
          fill: '#ddd',
          strokeWidth: 2,
          draggable: true,
          visible:false
        })
        layer.add(anchor)

        // add hover styling
        anchor.on('mouseover', function () {
          document.body.style.cursor = 'pointer'
          this.strokeWidth(4);
        });
        anchor.on('mouseout', function () {
          document.body.style.cursor = 'default'
          this.strokeWidth(2)
        });

        anchor.on('dragmove', function () {
          updateDottedLines()
        });

        return anchor;
      }

      //--------------- drawing elements--------------


      let stage = new Konva.Stage({
        container: 'aviator',
        width: width,
        height: height,
      });

      let layer = new Konva.Layer();
      stage.add(layer);
      
      //-------- backgound made of wages ---------------------
      let fontGroup = new Konva.Group({
        x: 0,
        y: height
      })
      // wedges

      let i
      let angle = 360/60

      for(i=0;i<60;i++) {

          let color = (i%2 === 1) ? '#0C0C0C': 'black'
          let wedge = new Konva.Wedge({
          id: 'wedge'+i,
          x: 0,
          y: 0,
          radius: 2.5*height,
          angle: angle,
          fill: color,
          stroke: 'black',
          strokeWidth: 1,
          rotation: angle*i + 182 - angle
          })
          wedge.shadowForStrokeEnabled(false)
          wedge.hitStrokeWidth(0)
          fontGroup.add(wedge)

      }
      layer.add(fontGroup)

      //---------- left dashed group -------------------
      let lPointsGroup = new Konva.Group({x: 0,y: -40,})
      for(i=0;i<8;i++) {

          let point = new Konva.Circle({
            x: 15,
            y: 40*i,
            radius:2,
            fill:"#1197D6"
          })
          point.shadowForStrokeEnabled(false)
          point.hitStrokeWidth(0)
          lPointsGroup.add(point)
      }
      layer.add(lPointsGroup)
      //---------- left dashed group -------------------
      let rPointsGroup = new Konva.Group({x: 160,y: 0})
      for(i=0;i<8;i++) {

          let point = new Konva.Circle({
            x: 80*i,
            y: height - 15,
            radius:2.2,
            fill:"white"
          })
          point.shadowForStrokeEnabled(false)
          point.hitStrokeWidth(0)
          rPointsGroup.add(point)
      }
      layer.add(rPointsGroup)
      //---------- plane groupe -------------------
      let aviGroup = new Konva.Group({
        x: 30,
        y: -30,
        height:height,
        width:width
      })
      let repere = new Konva.Line({
        x:0,
        y:0,
        points:[0,0,0,height,width,height],
        stroke: 'silver',
        tension:0,
        strokeWidth:1
      })
      aviGroup.add(repere)
      layer.add(aviGroup)

      // function to update line points from anchors
      function updateDottedLines() {
        var q = quad;
        //var b = bezier;

        quadLinePath.points([
          q.start.x(),
          q.start.y(),
          q.control.x(),
          q.control.y(),
          q.end.x(),
          q.end.y(),
        ]);

        /*console.log(q.start.x()+' ' + q.start.y() + ' ' +
        q.control.x() + ' ' + q.control.y() + ' ' +
        q.end.x() + ' ' + q.end.y())*/

      }

      // we will use custom shape for curve
      let quadraticLine = new Konva.Shape({
        stroke: 'yellow',
        strokeWidth: 4,
        sceneFunc: (ctx, shape) => {
          ctx.beginPath();
          ctx.moveTo(quad.start.x(), quad.start.y());
          ctx.quadraticCurveTo(
            quad.control.x(),
            quad.control.y(),
            quad.end.x(),
            quad.end.y()
          );
          ctx.fillStrokeShape(shape)
        },
      });
      aviGroup.add(quadraticLine)

      let quadLinePath = new Konva.Line({
        dash: [10, 10, 0, 10],
        strokeWidth: 3,
        stroke: 'black',
        lineCap: 'round',
        id: 'quadLinePath',
        opacity: 0.3,
        points: [0, 0],
      })


      // special objects to save references to anchors
      let quad = {
        start: buildAnchor(0, height),
        control: buildAnchor(327, 295),
        end: buildAnchor(467, 115)
      }
      this.quad = quad

      let line = new Konva.Line({
        x:0,
        y:0,
        points:[],
        stroke: '#F00B3E',
        tension:1,
        strokeWidth:8,
        lineCap:'round',
        closed:false
      })
      let lineRegion = new Konva.Line({
        x:0,
        y:0,
        points:[],
        tension:0,
        lineCap:'round',
        fill:'rgb(240, 11, 62,0.5)',
        closed:true
      })
      this.line = line
      this.lineRegion = lineRegion
      aviGroup.add(line)
      aviGroup.add(lineRegion)
      // get images plane
      let image = document.getElementById('image')
      let yoda = {}
      image.onload = function () {
        yoda = new Konva.Image({
          x: 1000,
          y: height-100,
          image: image,
          width: 119.5,
          height: 50.16
        });

        // add the shape to the layer
        aviGroup.add(yoda)
      };
      //----- some text on the screen -------------------
        let altText = new Konva.Text({
            x:width/2-80,
            y:height/2-50,
            text: '0x',
            fontSize: 100,
            fontStyle:"bold",
            fontFamily: 'Calibri',
            fill: 'white'
        })
        layer.add(altText)
        let endText = new Konva.Text({
          x:width/2-40,
          y:height/2-90,
          text: 'FLEW AWAY!',
          fontSize: 30,
          fontFamily: 'Calibri',
          fill: 'white'
      })
      layer.add(endText)
      endText.visible(false)
      this.altText = altText
      this.endText = endText
      // follow the fucking quadratic
      function getQuadraticBezierXY(percent,startPt,controlPt,endPt) {
        var x = Math.pow(1-percent,2) * startPt.x + 2 * (1-percent) * percent * controlPt.x + Math.pow(percent,2) * endPt.x; 
        var y = Math.pow(1-percent,2) * startPt.y + 2 * (1-percent) * percent * controlPt.y + Math.pow(percent,2) * endPt.y; 
        return( [x,y] );
        }

      function followBezier (percent) {
          let start = {x:quad.start.x(),y:quad.start.y()}
          let control = {x:quad.control.x(),y:quad.control.y()}
          let end = {x:quad.end.x(),y:quad.end.y()}
          let pl =getQuadraticBezierXY(percent,start,control,end)
          line.points(line.points().concat(pl))
          let lr = [0,height,...line.points(),pl[0],height]
          lineRegion.points(lr)
          yoda.x(pl[0]-10)
          yoda.y(pl[1]-50)
      }
    
      // let's animate a little bit
      //let amplitude = 1;
      //let period = 100;
      quadraticLine.visible(false)
      this.counter = 0
      this.step = 1 /*--- 1:the plane rise forward, 2:the plane goes down, 3: the plane goes up*/ 
      let k = this
      let planeMove = new Konva.Animation(function (frame) { 
        if(k.step == 1){
          followBezier(k.counter)
          k.counter += frame.frameRate/10000
          if(k.counter>1) {k.step = 2; k.counter = quad.end.y()}
        }
        else if(k.step == 2) {
          updateDottedLines()
          k.counter +=1
          quad.end.y(k.counter)
          quad.end.x(quad.end.x()-frame.frameRate/110)
          redraw()
          if(quad.end.y()>220) {k.step = 3; k.counter = quad.end.y()}
        }
        else {
          updateDottedLines()
          k.counter -= 1
          quad.end.y(k.counter)
          quad.end.x(quad.end.x()+frame.frameRate/100)
          redraw()
          if(quad.end.y()<140) { k.step = 2; k.counter = quad.end.y()}
        }
      }, layer)
      this.planeMove = planeMove
      function redraw () {
        let cc = -0.05
        line.points([])
        lineRegion.points([])
        while(cc<1) {
          cc +=0.05
          followBezier(cc)
        }
      }
      //----------- animation des textes -------------------------------------
      //-- number selection
      this.range =[{min:1,max:2},{min:1,max:2},{min:1,max:2},{min:1,max:2},{min:1,max:2},
        {min:2,max:3},{min:2,max:3},
        {min:3,max:5},
        {min:5,max:30},{min:5,max:30}]
      let range = this.range[Math.floor(Math.random()*9)]
      this.maxAlt = Math.random()*(range.max-range.min) + range.min
      this.altitude = 1
      let altitudeAnim = new Konva.Animation(function (frame) { 
        k.altitude += frame.frameRate/27000
        let a = (k.altitude*Math.sqrt(k.altitude/3)+0.41).toFixed(2)
        altText.text(a+'x')
        k.better[0].cashOut = (k.better[0].bet*a).toFixed(2)
        k.better[1].cashOut = (k.better[1].bet*a).toFixed(2)
        if(a > k.maxAlt) {
          altText.text(k.maxAlt.toFixed(2)+'x')
          this.stop()
          //backAnim.stop()
          planeMove.stop()
          altText.fill("#D0021B")
          endText.visible(true)
          yoda.x(1000)
          line.visible(false)
          lineRegion.visible(false) // out of the srceen
          k.launch(k)
        }
      }, layer)
      this.altitudeAnim = altitudeAnim
      //----------- backgroundAnimation -------------------------------------
      k.move = 0 
      k.move1 = 160
      this.backAnim = new Konva.Animation(function () { 
        fontGroup.rotate(-0.28)
        k.move += 0.9
        k.move1 -=0.9
        if(k.move > 40) k.move = 0
        if(k.move1 < 80) k.move1 = 160
        lPointsGroup.y(k.move)
        rPointsGroup.x(k.move1)
      }, layer)
      //this.backAnim = backAnim
      altText.fill("transparent")
      this.launch(this)


      rPointsGroup.cache()
      lPointsGroup.cache()
      fontGroup.cache()
    }
}
