import Aviator from './aviator/Aviator'
export default {
  name: 'aviaBox',
  components:{
    Aviator
  },
  data () {
      return {
        money:3000.00,
        results:[] // maxAlts
      }
  },
  methods:{
    cashing (cash) {
      this.money +=parseInt(cash)
    },
    maxAlt (max) {
      this.results.unshift(max.toFixed(2))
      if(this.results.length>14) this.results.pop()
    },
    color (maxAlt) {
      if(maxAlt<2) {
        return "#005D91"
      }
      else if(maxAlt<5) {
        return "#6B07D2"
      }
      else return "#900087"
    }
  }
}